import {
    Map,
    List
} from 'immutable';

const INITIAL_STATE = Map({
    user: Map(),
    isLogged: false,
    view: Map({
        isLoading: false,
        errorLoading: false,
        errorMessage: String
    })
});

export default function (state = INITIAL_STATE, action) {
    switch (action.type) {
        case 'GET_USER_SUCCESS':
            return state.merge({
                isLogged: true,
                user: action.payload
            });
        case 'GET_USER_FAILURE':
            return state.merge({
                view: {
                    isLoading: false,
                    errorLoading: true,
                    errorMessage: 'Wystpi bd podczas logowania!'
                }
            });
        case 'GET_USER_WRONG':
            return state.merge({
                view: {
                    isLoading: false,
                    errorLoading: true,
                    errorMessage: 'Ze haso lub login!'
                }
            });
        case 'ADD_USER_SUCCESS':
            return state.merge({
                view: {
                    errorLoading: false,
                    errorMessage: 'Pomylnie dodano uytkownika!'
                }
            });
        case 'ADD_USER_FAILURE':
            return state.merge({
                view: {
                    errorLoading: true,
                    errorMessage: 'Wystpi bd podczas dodawania uytkownika!'
                }
            });
        case 'LOGOUT':
            return state.merge({
                isLogged: false,
                user: {},
                view: {
                    errorLoading: false,
                    errorMessage: 'Pomylnie wylogowano!'
                }
            });
        default: return state;
    }
}
